.cardhead {
  color: #262626;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  margin-bottom: 4px;
}

.small {
  font-size: 17px;
  font-weight: 400;
  line-height: 20px;
  color: #666666;
}

.go-corner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 32px;
  height: 32px;
  overflow: hidden;
  top: 0;
  right: 0;
  background-color:  #4fc3f7;
  border-radius: 0 4px 0 32px;
}

.go-arrow {
  margin-top: -4px;
  margin-right: -4px;
  color: white;
  font-family: courier, sans;  
}

.card1 {
  display: block;
  position: relative;
  max-width: 90%;
  background-color: #f2f8f9;
  border-radius: 4px;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 3%;
  padding-bottom: 3%;
  margin: 20px;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
}


.card1::before{
    content: "";
    position: absolute;
    z-index: -1;
    top: -16px;
    right: -16px;
    background: #0093c4;
    height: 20px;
    width: 42px;
    border-radius: 3px;
    transform: scale(1);
    transform-origin: 50% 50%;
    transition: transform 0.25s ease-out;
}

.card1:hover::before{
  transform: scale(21);
}

.card1:hover h3 {
  transition: all 0.3s ease-out;
  color: #ffffff;
}


.card1:hover p {
  transition: all 0.3s ease-out;
  color: rgba(255, 255, 255, 0.8);
}

.card1:hover {
    transform: scale(1.15);
  }

.row1{
    display:flex;
}

@media (max-width:500px){
    .container{
        flex-flow: column;
        
    }
    .row1{
        flex-flow: column;
    }  
}